*,
html,
body,
#root,
.container {
	margin: 0px;
	padding: 0px;
	width: 100%;
	height: 100%;
	box-sizing: border-box;
}

.container {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
	width: 80%;
	margin: auto;
}

.modal {
	width: 40%;
	height: 70%;
	font-size: 30px;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
}

.modal:first-child {
	margin-right: 5px;
	width: 60%;
}

.bottom {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 100%;
	margin-top: 5px;
}

.box1 {
	background: blue;
}

.box2,
.box3 {
	background: green;
	padding: 30px;
	box-sizing: border-box;
}

.box2 {
	margin-right: 5px;
}
